.footer, .header, .icon {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.contact__title, .details__title, .bike__title, .page__section-title {
  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: -1.5px;
}
@media (min-width: 744px) {
  .contact__title, .details__title, .bike__title, .page__section-title {
    font-size: 48px;
    line-height: 48px;
  }
}
@media (min-width: 1260px) {
  .contact__title, .details__title, .bike__title, .page__section-title {
    font-size: 64px;
    line-height: 64px;
  }
}

.details__subtitle, .bike__price, .bike__subtitle {
  font-size: 24px;
  line-height: 34px;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.page {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  color: #fff;
  scroll-behavior: smooth;
  background-color: #1d1d1d;
}
.page__body {
  margin: 0;
}
.page__body--with-menu {
  overflow: hidden;
}
.page__menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
  transition: transform 0.3s;
}
.page__menu:target {
  transform: translateX(0);
}
.page__section {
  margin-bottom: 92px;
}
@media (min-width: 744px) {
  .page__section {
    margin-bottom: 132px;
  }
}
@media (min-width: 1260px) {
  .page__section {
    margin-bottom: 164px;
  }
}
.page__section-title {
  margin-bottom: 32px;
}

.logo {
  display: inline-flex;
}
.logo__image {
  height: 38px;
}
@media (min-width: 1260px) {
  .logo__image {
    height: 56px;
  }
}

.container {
  margin: 0 auto;
  max-width: 320px;
  padding: 0 20px;
}
@media (min-width: 744px) {
  .container {
    max-width: 600px;
  }
}
@media (min-width: 1260px) {
  .container {
    max-width: 1020px;
    padding: 0 40px;
  }
}

.icon {
  position: relative;
  display: block;
  width: 100%;
  padding-bottom: 100%;
  text-decoration: none;
}
.icon__tooltip {
  position: absolute;
  right: 100%;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  width: max-content;
  padding-right: 12px;
  color: #fff;
  font-style: 12px;
  line-height: 12px;
  letter-spacing: 2px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s;
}
.icon:hover .icon__tooltip {
  opacity: 1;
  pointer-events: all;
}
.icon:hover .icon__tooltip:hover {
  color: #000;
}
.icon--menu {
  background-image: url(../images/menu.svg);
  background-size: 100%;
  transition: background-image 0.3s;
}
.icon--menu:hover {
  background-image: url(../images/menu_hover.svg);
}
.icon--phone {
  background-image: url(../images/phone.svg);
}
.icon--close {
  background-image: url(../images/Vector.svg);
}

.top-action {
  --icon-width: 24px;
  --header-column-gap: 30px;
  display: grid;
  grid-template-columns: 1fr var(--icon-width) var(--icon-width);
  grid-column-gap: var(--header-column-gap);
  align-items: center;
}
@media (min-width: 1260px) {
  .top-action {
    --header-column-gap: 36px;
  }
}

.header {
  background-image: url(../images/Cover_photo.jpg);
}
.header__content {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  padding: 29px 0 90px;
}
@media (min-width: 744px) {
  .header__content {
    padding: 36px 0 130px;
  }
}
@media (min-width: 1260px) {
  .header__content {
    max-height: 710px;
    padding: 36px 0 200px;
  }
}
.header__title {
  margin: 0 auto;
  font-weight: bold;
  font-size: 36px;
  line-height: 36px;
  letter-spacing: -1.5px;
}
@media (min-width: 744px) {
  .header__title {
    font-size: 64px;
    line-height: 64px;
  }
}
@media (min-width: 1260px) {
  .header__title {
    font-size: 80px;
    line-height: 80px;
  }
}

.menu {
  background-color: #e5e5e5;
}
.menu__content {
  box-sizing: border-box;
  height: 100vh;
  padding: 29px 0;
  text-transform: uppercase;
}
@media (min-width: 1260px) {
  .menu__content {
    padding: 36px 0;
  }
}
.menu__action {
  grid-template-columns: 1fr var(--icon-width);
  margin-bottom: 40px;
}
.menu__list {
  margin: 0 0 40px;
  padding: 0;
  list-style: none;
}
.menu__item:not(:last-child) {
  margin-bottom: 24px;
}
.menu__link {
  display: inline-block;
  padding-bottom: 8px;
  border-bottom: 1px solid transparent;
  color: #1d1d1d;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
  transition: border-color 0.4s;
}
.menu__link:hover {
  border-color: #1d1d1d;
}
.menu__call {
  display: block;
  width: max-content;
  color: #1d1d1d;
  text-decoration: none;
}
.menu__call--phone {
  font-size: 16px;
  line-height: 27px;
  margin-bottom: 16px;
  border-bottom: 1px solid transparent;
}
.menu__call--phone:hover {
  border-color: #1d1d1d;
}
.menu__call--action {
  padding-bottom: 8px;
  border-bottom: 1px solid #1d1d1d;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
}

.footer {
  height: 200px;
  background-image: url(../images/footer.png);
}
@media (min-width: 744px) {
  .footer {
    height: 480px;
  }
}
@media (min-width: 1260px) {
  .footer {
    height: 555px;
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
}
@media (min-width: 744px) {
  .grid {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 24px;
  }
}
@media (min-width: 1260px) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
  }
}
.grid--desktop {
  display: block;
}
@media (min-width: 744px) {
  .grid--desktop {
    display: grid;
  }
}
.grid--tablet {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, 1fr);
  row-gap: 40px;
  margin: 0 auto;
}
@media (min-width: 744px) {
  .grid--tablet {
    width: 392px;
    row-gap: 48px;
  }
}
@media (min-width: 1260px) {
  .grid--tablet {
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr;
    max-width: 324px;
    margin: 0;
  }
}
