.about__title {
  font-size: 32px;
  line-height: 32px;
  letter-spacing: -1.5px;
}
@media (min-width: 744px) {
  .about__title {
    font-size: 48px;
    line-height: 48px;
  }
}
@media (min-width: 1260px) {
  .about__title {
    font-size: 64px;
    line-height: 64px;
  }
}
.about__p {
  font-size: 23px;
  font-weight: 500;
  line-height: 140%;
}

.bike__title {
  margin-bottom: 48px;
  text-align: center;
}
@media (min-width: 1260px) {
  .bike__title {
    margin-bottom: 56px;
  }
}
.bike__image {
  max-width: 280px;
  margin-bottom: 32px;
  transition: transform 0.5s;
}
@media (min-width: 744px) {
  .bike__image {
    max-width: 392px;
  }
}
@media (min-width: 1260px) {
  .bike__image {
    max-width: 324px;
  }
}
.bike__image:hover {
  transform: scale(1.2);
}
.bike__subtitle {
  margin-bottom: 8px;
}
.bike__text {
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}
.details__title {
  text-align: center;
  margin-bottom: 48px;
}
@media (min-width: 744px) {
  .details__title {
    margin-bottom: 64px;
  }
}
@media (min-width: 1260px) {
  .details__title {
    margin-bottom: 48px;
  }
}
.details__photo-container {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  max-height: 320px;
  margin-bottom: 16px;
}
.details__photo-container::after {
  content: "";
  display: block;
  padding-bottom: 100%;
}
@media (min-width: 744px) {
  .details__photo-container::after {
    height: 187px;
    padding-bottom: 0;
  }
}
@media (min-width: 1260px) {
  .details__photo-container::after {
    height: 320px;
  }
}
.details__photo {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: all 0.5s;
}
.details__photo:hover {
  transform: scale(1.05);
  box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  z-index: 1;
}
.details__description {
  margin-bottom: 40px;
}
@media (min-width: 744px) {
  .details__description {
    margin-bottom: 48px;
  }
}
.details__subtitle {
  margin-bottom: 8px;
}
.details__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 56px;
  border-radius: 40px;
  background-color: #fff;
  color: #1d1d1d;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s;
}
.button--primary {
  border: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
}
.button:hover {
  box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
}
.button:active {
  box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
}

.contact__title {
  text-align: center;
}
.contact__info {
  margin-bottom: 24px;
}
.contact__info-title {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 8px;
}
.contact__info-link {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #fff;
  text-decoration: none;
  transition: all 0.3s;
}
.contact__info-link:hover {
  color: #7e7e83;
}
.contact__input {
  margin-bottom: 16px;
}
.contact__input--last {
  margin-bottom: 32px;
}
.contact--mb {
  margin-bottom: 48px;
}

.form-field {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 48px;
  padding: 14px 16px;
  border-radius: 40px;
  color: #292929;
  font-size: 14px;
  font-family: inherit;
  line-height: 20px;
  background-color: #f8f8fa;
  border: 0;
  outline: none;
}
.form-field::placeholder {
  font-size: 14px;
  line-height: 20px;
  color: #7e7e83;
}
.form-field:hover {
  border-color: #d5d7de;
}
.form-field--text {
  height: 148px;
  font-family: inherit;
  border-radius: 16px;
  resize: none;
}
.form-field--text::-webkit-scrollbar {
  width: 25px;
}
.form-field--text::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 16px;
}
.form-field--text::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 20px;
}
.form-field--text::-webkit-scrollbar-thumb:hover {
  background: #555;
}

